export default {
	data() {
		return {
			radios: "../../static/images/radio.png",
			r: 0,
			phone: ""
		}
	},
	methods: {
		radiosFun() {
			if (this.r == 0) {
				this.radios = "../../static/images/radio-active.png"
				this.r = 1
			} else {
				this.radios = "../../static/images/radio.png"
				this.r = 0
			}
		},
		register(e) {
			let a = e.detail.value
			if (this.r == 0) {
				uni.showToast({
					title: "请阅读《用户注册须知》",
					icon: "none"
				});
			} else if (this.r == 1) {
				uni.request({
					url: "/api/Home/Public/reg",
					method: "POST",
					header: {
						'Content-Type': 'application/x-www-form-urlencoded'
					},
					data: a,
					success(res) {
						console.log(res)
						if (res.data.code == 0) {
							uni.showToast({
								title: res.data.data.title,
								icon: "none"
							});
						} else {
							uni.showToast({
								title: res.data.msg,
								icon: "none"
							});
						}
					}
				})
			}
		},
		verificationCode() {
			// this.phone
			uni.request({
				url: "/api/Home/Public/getcode",
				data: {
					tel: this.phone,
					type: "1"
				},
				success(res) {
					console.log(res)
					if (res.data.code == 0) {
						uni.showToast({
							title: res.data.data.title,
							icon: "none"
						});
					} else {
						uni.showToast({
							title: res.data.msg,
							icon: "none"
						});
					}
				}

			})
		},
		ntu() {
			uni.navigateTo({
				url: "../ntu/ntu"
			})
		},
		goLogin() {
			uni.redirectTo({
				url: "../index/index"
			})
		}

	},
}
